	module.exports = {"data":{"challengeNode":{"title":"Add a box-shadow to a Card-like Element","description":["<code>box-shadow</code>属性用来给元素添加阴影，该属性值是由逗号分隔的一个或多个阴影列表。","<code>box-shadow</code>属性的每个阴影依次由下面这些值描述：<ul><li><code>offset-x</code>阴影的水平偏移量；</li><li><code>offset-y</code>阴影的垂直偏移量;</li><li><code>blur-radius</code>模糊距离；</li><li><code>spread-radius</code>阴影尺寸；</li><li>颜色。</li></ul>其中<code>blur-raduis</code>和<code>spread-raduis</code>是可选的。","下面是创建了多个阴影的 CSS 例子，阴影加了模糊效果，颜色是透明度很高的黑色:","<blockquote>box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);</blockquote>","<hr>","现在卡片已经添加了 id<code>thumbnail</code>。把上面的<code>box-shadow</code>值赋给卡片。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/applied-visual-design/add-a-box-shadow-to-a-card-like-element","blockName":"Applied Visual Design","tests":[{"text":"你应该给 id 为<code>thumbnail</code>的元素添加<code>box-shadow</code>属性。","testString":"assert(code.match(/#thumbnail\\s*?{\\s*?box-shadow/g), '你应该给 id 为<code>thumbnail</code>的元素添加<code>box-shadow</code>属性。');"},{"text":"<code>box-shadow</code>值应该是指定的 CSS 值。","testString":"assert(code.match(/box-shadow:\\s*?0\\s+?10px\\s+?20px\\s+?rgba\\(\\s*?0\\s*?,\\s*?0\\s*?,\\s*?0\\s*?,\\s*?0?\\.19\\),\\s*?0\\s+?6px\\s+?6px\\s+?rgba\\(\\s*?0\\s*?,\\s*?0\\s*?,\\s*?0\\s*?,\\s*?0?\\.23\\)/gi), '<code>box-shadow</code>值应该是指定的 CSS 值。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  h4 {\n    text-align: center;\n    background-color: rgba(45, 45, 45, 0.1);\n    padding: 10px;\n    font-size: 27px;\n  }\n  p {\n    text-align: justify;\n  }\n  .links {\n    text-align: left;\n    color: black;\n  }\n  \n  \n  \n  .fullCard {\n    width: 245px;\n    border: 1px solid #ccc;\n    border-radius: 5px;\n    margin: 10px 5px;\n    padding: 4px;\n  }\n  .cardContent {\n    padding: 10px;\n  }\n  .cardText {\n    margin-bottom: 30px;\n  }\n</style>\n<div class=\"fullCard\" id=\"thumbnail\">\n  <div class=\"cardContent\">\n    <div class=\"cardText\">\n      <h4>Alphabet</h4>\n      <hr>\n      <p><em>Google 由在<strong>斯坦福大学</strong>攻读<u>理工博士</u>的拉里·佩奇和谢尔盖·布林共同创建。</em></p>\n    </div>\n    <div class=\"cardLinks\">\n      <a href=\"https://en.wikipedia.org/wiki/Larry_Page\" target=\"_blank\" class=\"links\">拉里·佩奇</a><br><br>\n      <a href=\"https://en.wikipedia.org/wiki/Sergey_Brin\" target=\"_blank\" class=\"links\">谢尔盖·布林</a>\n    </div>\n  </div>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/applied-visual-design/decrease-the-opacity-of-an-element","id":"587d781b367417b2b2512abe"},"slug":"/responsive-web-design/applied-visual-design/add-a-box-shadow-to-a-card-like-element"}}

